<template>
  <div id="app">
    <Header
      :numCorrect="numCorrect"
      :numTotal="numTotal"
    />
    <b-container class="bv-example-row">
      <b-row>
        <b-col md="6" sm="12" offset-md="3">
          <QuestionBox
            v-if="questions.length"
            :currentQuestion="questions[index]"
            :next="next"
            :increment="increment"
            :maxQuestions="questions.length <= numTotal"
          />
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
import Header from "./components/Header";
import QuestionBox from "./components/QuestionBox";

export default {
  name: "app",
  components: {
    Header,
    QuestionBox
  },
  data() {
    return {
      questions: [],
      index: 0,
      numCorrect: 0,
      numTotal: 0,
    }
  },
  methods: {
    async getQuestions() {
      const res = await fetch(`https://opentdb.com/api.php?amount=10&category=11&type=multiple`, { method: 'GET' })
      const data = await res.json()
      this.questions = data.results
    },
    next() {
      this.index++
    },
    increment(isCorrect) {
      if (isCorrect) this.numCorrect++
      
      this.numTotal++
    }
  },
  mounted() {
    this.getQuestions()
  }
};
</script>

