<template>
  <div class="question-box-container">
    <b-jumbotron>
      <template slot="lead">
        <b v-html="currentQuestion.question"></b>
      </template>

      <hr class="my-4" />

      <b-list-group>
        <b-list-group-item
          v-for="(answer,index) in shuffledAnswers"
          :key="index"
          variant="primary"
          href="#"
          @click="selectAnswer(index)"
          :class="answerClass(index)"
          v-html="answer"
        ></b-list-group-item>
      </b-list-group>
      <b-row>
        <b-col sm="6">
          <b-button
            pill block
            class="mt-2 mt-md-0"
            variant="primary"
            href="#"
            @click="submitAnswer"
            :disabled="selectedIndex === null || answered"
          >Submit</b-button>
        </b-col>
        <b-col sm="6">
          <b-button class="mt-2 mt-md-0" pill block @click="next" :disabled="maxQuestions" variant="success" href="#">Next</b-button>
        </b-col>
        <b-col v-if="maxQuestions" sm="12">
          <b-button @click="playAgain" pill block class="mt-2" variant="danger" href="#">Play Again</b-button>
        </b-col>
      </b-row>
    </b-jumbotron>
  </div>
</template>

<script>
import _ from "lodash";

export default {
  props: {
    currentQuestion: Object,
    next: Function,
    increment: Function,
    maxQuestions: Boolean,
  },
  data() {
    return {
      selectedIndex: null,
      correctIndex: null,
      shuffledAnswers: [],
      answered: false
    };
  },
  watch: {
    currentQuestion: {
      immediate: true,
      handler() {
        this.selectedIndex = null;
        this.answered = false;
        this.shuffleAnswers();
      }
    }
  },
  computed: {
    answers() {
      let answers = [...this.currentQuestion.incorrect_answers];
      answers.push(this.currentQuestion.correct_answer);
      return answers;
    },
  },
  methods: {
    playAgain() {
      window.location.reload()
    },
    selectAnswer(index) {
      this.selectedIndex = index;
    },
    submitAnswer() {
      let isCorrect = false;
      if (this.selectedIndex === this.correctIndex) isCorrect = true;
      this.answered = true;
      this.increment(isCorrect);
    },
    shuffleAnswers() {
      let answers = [
        ...this.currentQuestion.incorrect_answers,
        this.currentQuestion.correct_answer
      ];
      this.shuffledAnswers = _.shuffle(answers);
      this.correctIndex = this.shuffledAnswers.indexOf(
        this.currentQuestion.correct_answer
      );
    },
    answerClass(index) {
      return [
        !this.answered && this.selectedIndex == index ? 'selected' :
        this.answered && this.correctIndex === index ? 'correct' :
        this.answered && this.selectedIndex == index && this.correctIndex !== index ? 'incorrect' : ''
      ]
    }
  }
};
</script>

<style lang="scss">
.list-group {
  margin-bottom: 15px;
}
.btn {
  margin: 0 5px;
}
.selected {
  background-color: lighten(blue, 25);
}
.correct {
  background-color: lighten(green, 20);
}
.incorrect {
  background-color: lighten(red, 20);
}
</style>
